<!-- eslint-disable max-len -->
<template>
  <div>
    <ul class="cart__orders">
      <li class="cart__order" v-for="item in cartInfo" :key="item.id">
        <h3>{{ item.product.title }}</h3>
        <div><b>{{ (item.product.price * (item.quantity || item.amount)) | numberFormat }} ₽</b><br />{{( item.quantity || item.amount )}} шт.</div>
        <span>Артикул: {{ item.product.id }}</span>
      </li>
    </ul>

    <div class="cart__total">
      <p>Доставка: <b>500 ₽</b></p>
      <p>Итого: <b>{{ productsCount | countProductsStr }}</b> на сумму <b>{{ totalPrice | numberFormat }} ₽</b>
      </p>
    </div>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import countProductsStr from '@/helpers/countProductsStr';

export default {
  name: 'CartContentList',
  components: {},
  props: ['cartInfo'],
  model: {},
  data() {
    return {};
  },
  computed: {
    productsCount() {
      return this.cartInfo.length;
    },
    totalPrice() {
      // eslint-disable-next-line max-len
      return this.cartInfo.reduce((sum, item) => sum + item.product.price * (item.quantity || item.amount), 0);
    },
  },
  filters: { numberFormat, countProductsStr },
  watch: {},
  methods: {},
  created() { },
  mounted() { },
};
</script>
