<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <li class="catalog__item" v-show="checkProduct()">
    <router-link class="catalog__pic" :to="{name: 'product', params: {id: product.id}}"> <!-- ! используем роутер для перехода -->
      <img :src="product.image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat }} ₽
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="productColor in product.colors" :key="(product.id + productColor.id)">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="productColor.id" v-model="color">
          <span class="colors__value" :style="{'background-color': productColor.code}" :title="productColor.title">
          </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
// import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat';

export default {
  name: 'ProductItem',
  props: ['product'], // ! входные параметры - отдельный продукт
  data() {
    return {
      color: null,
    };
  },
  methods: {
    checkProduct() {
      return this.product && typeof this.product === 'object' && this.product.title;
    },
    // gotoPage,
  },
  filters: {
    numberFormat,
  },
  mounted() {
    // подключаем из всех возможных цветов тот, который присущ данному товару
    // здесь наблюдатели не нужны, т.к. продукт уже загружен на главной странице
    this.color = this.product.colors[0].id;
  },
};
</script>
