<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ productsCount | countProductsStr }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" title="ФИО" :error="formError.name" pid="name" type="text"
              placeholder="Введите ваше полное имя" />
            <BaseFormText v-model="formData.address" title="Адрес доставки" :error="formError.address" pid="address"
              type="text" placeholder="Введите ваш адрес" />
            <BaseFormText v-model="formData.phone" title="Телефон" :error="formError.phone" pid="phone" type="tel"
              placeholder="Введите ваш телефон" />
            <BaseFormText v-model="formData.email" title="Email" :error="formError.email" pid="email" type="email"
              placeholder="Введите ваш Email" />
            <BaseTextArea v-model="formData.comment" title="Комментарий к заказу" :error="formError.comment" pid="name"
              placeholder="Ваши пожелания" />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label" for="delivery-item-1">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked=""
                    id="delivery-item-1">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label" for="delivery-item-2">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500" id="delivery-item-2">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label" for="payment-item-1">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card" id="payment-item-1">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label" for="payment-item-2">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash" id="payment-item-2">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <CartContent :cartInfo="products" />

          <button class="cart__button button button--primery" type="submit"
            style="display:flex;align-items:center;justify-content:center">
            <span style="padding-top:4px;margin:auto 10px">Оформить заказ</span>
            <vue-simple-spinner size="medium" style="display:inline-block;" v-show="orderCreating" />
          </button>
          <p v-show="orderCreated" style="text-align:center">Заявка отпралена!</p>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import countProductsStr from '@/helpers/countProductsStr';
import VueSimpleSpinner from 'vue-simple-spinner';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseTextArea from '@/components/BaseTextArea.vue';
import { mapGetters } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import CartContent from '@/components/CartContent.vue';

export default {
  name: 'OrderPage',
  components: {
    BaseFormText, BaseTextArea, VueSimpleSpinner, CartContent,
  },
  props: [],
  model: {},
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      orderCreated: false,
      orderCreating: false,
    };
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      productsCount: 'cartProductsCount',
    }),
  },
  filters: { countProductsStr },
  watch: {},
  methods: {
    async order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.orderCreated = false;
      this.orderCreating = true;
      // eslint-disable-next-line no-promise-executor-return
      return (new Promise((resolve) => setTimeout(resolve, 1000))) // для теста
        .then(() => axios
          .post(
            `${API_BASE_URL}/api/orders`,
            { ...this.formData },
            { params: { userAccessKey: this.$store.state.userAccessKey } },
          )
          .then((response) => {
            this.orderCreated = true;
            this.$store.commit('resetCart');
            // обновить инфо о добавленном заказе в хранилище и перейти на страницу заказа
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
          })
          .catch((error) => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
          .finally(() => {
            this.orderCreating = false;
          }));
    },
  },
  created() { },
  mounted() { },
};
</script>
