<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }"> <!-- ! предпочтительный для HTML способ -->
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }"> <!-- ! предпочтительный для HTML способ -->
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ this.orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <BaseDictItem fieldName="Получатель" :fieldValue="orderInfo.name" />
            <BaseDictItem fieldName="Адрес доставки" :fieldValue="orderInfo.address" />
            <BaseDictItem fieldName="Телефон" :fieldValue="orderInfo.phone" />
            <BaseDictItem fieldName="Email" :fieldValue="orderInfo.email" />
            <BaseDictItem fieldName="Способ оплаты" :fieldValue="orderInfo.payment" />
          </ul>
        </div>

        <div class="cart__block">
          <CartContent :cartInfo="cartInfo"/>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseDictItem from '@/components/BaseDictItem.vue';
import CartContent from '@/components/CartContent.vue';
import numberFormat from '@/helpers/numberFormat';
import countProductsStr from '@/helpers/countProductsStr';

export default {
  name: 'OrderInfoPage',
  components: { BaseDictItem, CartContent },
  props: [],
  model: {},
  data() {
    return {};
  },
  computed: {
    orderInfo() {
      return {
        payment: 'картой при получении', // по умолчанию
        ...this.$store.state.orderInfo || {},
      };
    },
    cartInfo() {
      return this.orderInfo.basket?.items || [];
    },
  },
  filters: { numberFormat, countProductsStr },
  watch: {},
  methods: {},
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      return; // не выполняем извлечение дважды, т.к. после создания данные уже извлечены в $store
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
  mounted() { },
};
</script>
