<!-- eslint-disable max-len -->
<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a href="#" class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': isFirstPage}" @click.prevent="paginate(page - 1)" aria-label="Предыдущая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNum in pages" :key="pageNum">
      <a href='#' class="pagination__link" :class="{'pagination__link--current': pageNum === page}" @click.prevent="paginate(pageNum)">
        {{pageNum}}
      </a>
    </li>
    <li class="pagination__item">
      <a href="#" class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': isLastPage}" @click.prevent="paginate(page + 1)" aria-label="Следующая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() { return Math.ceil(this.count / this.perPage); },
    isFirstPage() { return this.page === 1; },
    isLastPage() { return this.page === this.pages; },
  },
  methods: {
    paginate(page) {
      if (this.isPageExists(page)) {
        this.$emit('paginate', page);
      }
    },
    isPageExists(num) { return num >= 1 && num <= this.pages; },
  },
};
</script>
