<script>
import VueSimpleSpinner from 'vue-simple-spinner';
import { API_BASE_URL } from '@/config';
import axios from 'axios';
import numberFormat from '@/helpers/numberFormat';
import BaseCounter from '@/components/BaseCounter.vue';
import { mapActions } from 'vuex';

export default {
  name: 'ProductPage',
  components: { BaseCounter, VueSimpleSpinner },
  data() {
    return {
      color: null,
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,
      productAdded: false,
      productAdding: false,
    };
  },
  computed: {
    product() {
      // eslint-disable-next-line max-len
      // return products.find((item) => item.id === +this.$route.params.id); // ищем динамический параметр из текущего маршрута, приводим к числу
      return this.productData
        ? {
          ...this.productData,
          title: this.productData.title,
          image: this.productData.image.file.url,
        }
        : {};
    },
    category() {
      return this.productData ? this.productData.category : '';
    },
  },
  watch: {
    product() {
      // здесь нужен наблюдатель, т.к. продукт требует времени для загрузки
      this.color = this.product.colors[0].id;
    },
    // ! наблюдаем за динамическим сегментом роутера - за его параметрами!
    '$route.params.id': {
      handler() { this.loadProduct(); },
      immediate: true,
    },
  },
  filters: {
    numberFormat,
  },
  methods: {
    ...mapActions(['addProductToCart']),

    addToCart() {
      this.productAdded = false;
      this.productAdding = true;
      // ! асинхронные функции возвращают промисы!
      this.addProductToCart({ productId: this.product.id, amount: this.productAmount })
        .then(() => {
          this.productAdded = true;
        })
        .catch(() => {})
        .finally(() => {
          this.productAdding = false;
        });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      return axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        .then((response) => {
          this.productData = response.data;
        })
        .catch(() => {
          this.productLoadingFailed = true;
        })
        .finally(() => {
          this.productLoading = false;
        });
    },
  },
};
</script>
