<!-- eslint-disable max-len -->
<template>
  <div>
    <main class="content container" v-if="cartLoading"><vue-simple-spinner size="medium" /></main>
    <main class="content container" v-else-if="cartLoadingFailed">Произошла ошибка при загрузке корзины</main>
    <main class="content container" v-else>
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              Корзина
            </a>
          </li>
        </ul>

        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info">
          {{ productsCount | countProductsStr }}
        </span>
      </div>

      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <ul class="cart__list">
              <CartItem v-for="item in products" :key="item.productId" :item="item"/>
            </ul>
          </div>

          <div class="cart__block">
            <p class="cart__desc">
              Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
            </p>
            <p class="cart__price">
              Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
            </p>

            <router-link tag="button" :to="{ name: 'order' }" class="cart__button button button--primery" type="submit" :style="`display:${buttonCreateOrderDisplay}`">
              Оформить заказ
            </router-link>
          </div>
        </form>
      </section>
    </main>
  </div>
</template>

<script>
import VueSimpleSpinner from 'vue-simple-spinner';
import countProductsStr from '@/helpers/countProductsStr';
import numberFormat from '@/helpers/numberFormat';
import CartItem from '@/components/CartItem.vue';
import { mapGetters, mapState } from 'vuex';

export default {
  components: { CartItem, VueSimpleSpinner },
  filters: {
    numberFormat,
    countProductsStr,
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      itemsCount: 'cartProductsItemsCount',
      productsCount: 'cartProductsCount',
    }),
    ...mapState(['cartLoading', 'cartLoadingFailed']),
    buttonCreateOrderDisplay() {
      if (this.productsCount === 0) return 'none';
      return 'inherit';
    },
  },
};
</script>
