<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{ countProducts | countProductsStr }}
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId" :color-id.sync="filterColorId" :page.sync="page" />

      <section class="catalog">
        <!-- ! работа с тормозами и возможными ошибками -->
        <div v-if="productsLoading" style="text-align:center; margin-bottom: 5px;">
          <vue-simple-spinner size="medium"/>
        </div>
        <div v-if="productsLoadingFailed" style="text-align:center">
          Произошла ошибка при загрузке товаров: {{productsLoadingError}}
          <button @click.prevent="loadProducts">Попробовать еще раз</button>
        </div>

        <ProductList :products="products" />
        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>

<script>
import VueSimpleSpinner from 'vue-simple-spinner';
import { API_BASE_URL } from '@/config';
import axios from 'axios';
import countProductsStr from '@/helpers/countProductsStr';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';

export default {
  name: 'MainPage',
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: this.$route.params.categoryId || 0,
      filterColorId: null,
      page: 1,
      productsPerPage: 6,
      productsData: null,
      productsLoading: false, // статус загрузки страницы
      productsLoadingFailed: false, // ошибка при загрузке страницы
      productsLoadingErrorCode: null,
    };
  },
  computed: {
    products() {
      // const startIndex = (this.page - 1) * this.productsPerPage;
      // return this.filteredProducts.slice(startIndex, startIndex + this.productsPerPage);
      return this.productsData
        ? this.productsData.items.map((item) => ({ ...item, image: item.image.file.url }))
        : [];
    },
    countProducts() {
      // return this.filteredProducts.length;
      return this.productsData ? this.productsData.pagination.total : 0;
    },
    productsLoadingError() {
      if (this.productsLoadingErrorCode === 404) {
        return 'некорректный адрес сервера';
      }
      return 'неизвестная ошибка';
    },
  },
  watch: {
    // перезапрос данных, если изменилась страница или фильтры
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  filters: {
    countProductsStr,
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      // таймер для того, чтобы при изменении фильтров не вызывалась загрузка несколько раз подряд
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(
            `${API_BASE_URL}/api/products`,
            {
              params: {
                page: this.page,
                limit: this.productsPerPage,
                categoryId: this.filterCategoryId,
                minPrice: this.filterPriceFrom,
                maxPrice: this.filterPriceTo,
                colorId: this.filterColorId,
              },
            },
          )
          .then((response) => {
            this.productsData = response.data;
          })
          .catch((error) => {
            this.productsLoadingErrorCode = error.response.status;
            this.productsLoadingFailed = true;
          })
          .finally(() => { this.productsLoading = false; });
      }, 500); // --> для теста
    },
  },
  components: {
    ProductList,
    BasePagination,
    ProductFilter,
    VueSimpleSpinner,
  },
  created() {
    this.loadProducts();
  },
};
</script>
