<!-- eslint-disable max-len -->
<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ item.product.id }}
    </span>

    <BaseCounter min-val="1" v-model.number="amount" /> <!-- ! обязательно пользуемся вычисляемым свойством, т.к. не можем напрямую изменять значение в хранилище! -->

    <b class="product__price">
      {{ (item.product.price * amount) | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteCartProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import BaseCounter from '@/components/BaseCounter.vue';
import { mapActions } from 'vuex';

export default {
  name: 'CartItem',
  components: { BaseCounter },
  props: ['item'],
  model: {},
  data() {
    return {};
  },
  computed: {
    amount: {
      get() {
        return this.item.amount; // ! извлекаем значение из корзины продуктов
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value }); // ! вызываем мутацию в корзине продуктов
      },
    },
  },
  filters: { numberFormat },
  watch: {},
  methods: {
    ...mapActions(['deleteCartProduct']),
  },
  created() {},
  mounted() {},
};
</script>
